import data from "../data";

export const DataContext = React.createContext();

const DataContextProvider = ({ children, innovations, humans, about }) => {
  console.log("DataContextProvider", innovations, humans, about);
  const humanData = humans.results.map(human => ({
    ...human.data,
    id: human.id,
    uid: human.uid
  }));

  const innovationsData = innovations.results.map(innovation => ({
    ...innovation.data,
    id: innovation.id,
    uid: innovation.uid
  }));

  const aboutData = about;

  return (
    <DataContext.Provider
      value={{
        components: data.components,
        humans: humanData,
        innovations: innovationsData,
        about: aboutData
      }}
    >
      {children}
    </DataContext.Provider>
  );
};

export default DataContextProvider;
